'use client'

import { useRouter } from "next/router";
import { useState } from "react";

export default function AdminMenu() {

  const router = useRouter();

  const opciones = [
    { nombre: 'Agregar Producto', ruta: '/admin/agregar'},
    { nombre: 'Eliminar Producto', ruta: '/admin/eliminar'},
    { nombre: 'Actualizar Producto', ruta: '/admin/actualizar'},
  ];

  return (
    <main className="p-8 max-w-xl mx-auto text-center text-black">
      <h1 className="text-3xl font-bold mb-6 ">Panel de Administraci√≥n</h1>
      <div className="grid gap-4">
        {opciones.map(op => (
          <button 
            key={op.nombre}
            onClick={() => router.push(op.ruta)}
            className="bg-orange-500 hover:bg-orange-600 text.white py-3 px-6 rounded shadow"
          >
            {op.nombre}
          </button>
        ))}
      </div>
    </main>
  );
}
